.list {
     list-style-type: none;
     padding: 0px;
    &__item {
        background-color: rgb(228, 228, 228);
        border-radius: 15px;
        border: 1px solid rgb(88, 88, 88);
        padding: 10px;
        margin-top: 20px;
        
        &-complited {
            border-radius: 15px;
            background-color: rgb(243, 242, 242);
            border: 1px solid rgb(255, 255, 255);
            padding: 10px;
            margin-top: 20px;
        }
        &-date {
            margin-top: 10px;
        }
        &-file {
            margin-top: 10px;
        }
    }
}

.title-complited {
    text-decoration: line-through;
}

